}


.Experience-card {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 272px;
    height: 272px;

    margin: 30px;

    background: white;
    border-radius: 22px;

    cursor: pointer;
    -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 1s;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */


